import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";

export default function LandingPage() {
  const plans = [
    {
      name: "Free",
      price: "R$ 0",
      details: "Até 5 pacientes",
      cta: "Começar grátis",
    },
    {
      name: "Core",
      price: "R$ 79,90/mês",
      details: "Pacientes ilimitados + templates",
      cta: "Escolher Core",
    },
    {
      name: "Pro",
      price: "R$ 99,90/mês",
      details: "Tudo do Core + analytics",
      cta: "Escolher Pro",
    },
  ];

  return (
    <main className="mx-auto flex w-full max-w-6xl flex-col gap-12 px-6 py-10">
      <header className="flex items-center justify-between">
        <h1 className="text-lg font-semibold tracking-tight">KoreVitta</h1>
        <nav className="flex items-center gap-3">
          <Link href="/login">
            <Button variant="ghost">Entrar</Button>
          </Link>
          <Link href="/register">
            <Button>Começar grátis</Button>
          </Link>
        </nav>
      </header>

      <section className="kv-surface rounded-3xl p-8 md:p-12">
        <p className="kv-pill inline-flex">PUB-01 • SaaS Experience</p>
        <h2 className="mt-4 max-w-3xl text-3xl font-semibold tracking-tight md:text-5xl">
          A forma mais simples de organizar o acompanhamento nutricional online.
        </h2>
        <p className="mt-4 max-w-2xl text-base text-muted-foreground">
          Menos operação repetitiva para o profissional e mais clareza diária para o paciente.
        </p>
        <div className="mt-6 flex flex-wrap gap-3">
          <Link href="/register">
            <Button size="lg">Começar grátis</Button>
          </Link>
          <Link href="/dashboard">
            <Button size="lg" variant="outline">
              Ver área profissional
            </Button>
          </Link>
        </div>
      </section>

      <section className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader>
            <CardTitle>Organização</CardTitle>
            <CardDescription>CRM com visão diária de risco, check-ins e adesão.</CardDescription>
          </CardHeader>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Execução</CardTitle>
            <CardDescription>Protocolos integrados e tela “Hoje” orientada à ação.</CardDescription>
          </CardHeader>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle>Evolução</CardTitle>
            <CardDescription>Progresso visual para retenção e percepção de resultado.</CardDescription>
          </CardHeader>
        </Card>
      </section>

      <section className="grid gap-4 md:grid-cols-3">
        {plans.map((plan) => (
          <Card key={plan.name}>
            <CardHeader>
              <CardTitle>{plan.name}</CardTitle>
              <CardDescription>{plan.price}</CardDescription>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-sm text-muted-foreground">{plan.details}</p>
              <Link href="/register">
                <Button className="w-full" variant={plan.name === "Free" ? "outline" : "default"}>
                  {plan.cta}
                </Button>
              </Link>
            </CardContent>
          </Card>
        ))}
      </section>

      <section className="kv-surface rounded-3xl p-8">
        <h3 className="text-xl font-semibold">FAQ rápido</h3>
        <div className="mt-4 space-y-4 text-sm text-muted-foreground">
          <p>
            <span className="font-medium text-foreground">Preciso instalar app?</span> Não. Web app
            responsivo para profissional e paciente.
          </p>
          <p>
            <span className="font-medium text-foreground">Posso começar no plano Free?</span> Sim,
            com até 5 pacientes.
          </p>
          <p>
            <span className="font-medium text-foreground">Tem templates de protocolo?</span> Sim,
            disponível na evolução do produto (V1.1+).
          </p>
        </div>
      </section>
    </main>
  );
}
